    var monthDiff = now.getMonth() - birth.getMonth();
    var dateDiff = now.getDate() - birth.getDate();
    //不足一月退位
    if (dateDiff < 0) {
      dateDiff = days + dateDiff;
      monthDiff--;
    }
    //不足一年退位
    if (monthDiff < 0) {
      monthDiff = 12 + monthDiff;
      yearDiff--;
    }
